.container {
	margin: 0;
	padding: 4rem 6rem;
	display: flex;
	align-items: flex-start;
}

.wrapper {
	flex-basis: 93%;
	margin: 0;
	padding: 0;
}

.inner {
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: center;
	margin: 0 0 4rem 0;
}

.contacts {
	flex-basis: 7%;
	margin: 0;
	padding: 0;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.collocutor {
	
}

.activeCollocutor {
	background-color: purple;
}

.message {
	display: flex;
	flex-wrap: nowrap;
	margin: 0;
	padding: 1rem 1.5rem;
}

.userpic {
	border-radius: 50%;
	width: 50px;
	height: auto;
}

.messageText {
	
}

.form {
	display: flex;
	justify-content: space-between;
	align-items: center;
}

